@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --main-color: #036058;
  --text-light: #f1f1f1;
  --chat-bg: #ece5dd;
  --bg-color: #5fc4d0;
}

body {
  background-color: #5fc4d0;
  background-color: var(--bg-color);
  font-family: 'Roboto', sans-serif;
}

.chat-container {
  /*width: 350px;*/
  width: 308px;
  height: 567px;
  background-image: url(/static/media/chat-pattern.d05d0c7b.svg);
  overflow: hidden;
  display: flex;
  flex-direction: column;
  position: absolute;
  top: 92px;
  left: 494px;
}

.header {
  display: flex;
  justify-content: flex-start;
  height: 50px;
  background-color: #036058;
  background-color: var(--main-color);
  align-items: center;
  padding-left: 5px;
  padding-right: 15px;
  overflow: hidden;
}

.dots {
  margin-left: auto;
}

.icons {
  color: #f1f1f1;
  color: var(--text-light);
}

.chat-name-container h4 {
  font-weight: 500;
  color: #f1f1f1;
  color: var(--text-light);
  margin: 0;
}

.chat-name-container span {
  color: #f1f1f1;
  color: var(--text-light);
  font-weight: 500;
  font-size: 13px;
}

.photo-container {
  height: 30px;
  width: 30px;
  border-radius: 50%;
  margin-right: 8px;
  background-color: #e1feff;
  overflow: hidden;
}

.messages-wrapper {
  flex: 1 1;
  padding-top: 10px;
  padding-bottom: 10px;
  overflow-y: scroll;
}

.messages-wrapper::-webkit-scrollbar {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
  width: 4px;
  height: 1px;
}

.messages-wrapper::-webkit-scrollbar-track {
  background: transparent;
}

.messages-wrapper::-webkit-scrollbar-thumb {
  background: #b3ada7;
}

.messages-area {
  display: flex;
  flex-direction: column;
}

.message-entry-area {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  width: 100%;
}

.message-container {
  display: flex;
  align-items: center;
  flex: 1 1;
  height: 37px;
  margin-left: 10px;
  margin-bottom: 3px;
  border-radius: 21px;
  background-color: #fff;
}

#send-button {
  display: flex;
  align-items: center;
  height: 37px;
  margin: 0 10px 3px 9px;
  padding: 7px;
  border: none;
  border-radius: 50%;
  background-color: #008a7a;
  box-shadow: 0 0 1px 1px rgba(0, 0, 0, 0.2);
  outline: none;
}

.icon-off {
  font-size: 26px;
  color: #9ea2a3;
}

.message-container span {
  margin-left: 7px;
}

#new-message {
  outline: none;
  border: none;
  font-size: 16px;
  margin: 5px;
  width: 100%;
}

#new-message::-webkit-input-placeholder {
  color: rgba(34, 32, 9, 0.4);
}

#new-message:-ms-input-placeholder {
  color: rgba(34, 32, 9, 0.4);
}

#new-message::-ms-input-placeholder {
  color: rgba(34, 32, 9, 0.4);
}

#new-message::placeholder {
  color: rgba(34, 32, 9, 0.4);
}

@media only screen and (max-height: 900px), (max-width: 992px) {
  .chat-container {
    width: 100vw;
    height: 100vh;
    border: none;
    position: static;
  }
}

:root {
  --guest-bubble-color: #e2ffc7;
}

.bubble-container {
  display: inline-flex;
  max-width: 80%;
  margin-bottom: 1px;
  padding: 0 3px 3px 20px;
  overflow: hidden;
}

.bc-guest {
  align-self: flex-end;
  padding: 0 20px 3px 3px;
}

.bubble {
  display: inline-flex;
  position: relative;
  padding: 5px 7px;
  background-color: #ffffff;
  border-radius: 5px;
  box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.1);
}

.b-guest {
  background-color: #e2ffc7;
  background-color: var(--guest-bubble-color);
}

.tail-h .bubble:after {
  content: '';
  position: absolute;
  top: -10px;
  left: -6px;
  width: 18px;
  height: 18px;
  transform: rotate(45deg);
  border-bottom-left-radius: 5px;
  background-color: #fff;
  visibility: visible;
}

/**/

.tail-g .bubble:after {
  content: '';
  position: absolute;
  top: -10px;
  right: -10px;
  width: 18px;
  height: 18px;
  transform: rotate(45deg);
  border-top-right-radius: 5px;
  border-right: 1px solid rgba(0, 0, 0, 0.3);
  background-color: #e2ffc7;
  background-color: var(--guest-bubble-color);
  visibility: visible;
}

/**/

.message {
  font-size: 15px;
  word-break: break-word;
}

.time-with-seen {
  display: flex;
  align-items: flex-end;
  font-size: 12px;
  color: rgba(0, 0, 0, 0.5);
  margin-left: 10px;
}

svg {
  margin-left: 3px;
  margin-bottom: 2px;
}

a {
  color: #008a7a;
  font-weight: 400;
  text-decoration: none;
}

.photoMessage {
  width: 100%;
}

.AppWrapper {
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

.AppContainer {
  display: flex;
  height: 100vh;
  position: relative;
}

@media only screen and (min-height: 900px) and (min-width: 992px) {
  .AppContainer {
    height: 980px;
    width: 1400px;
    margin: 0 auto;
    background-image: url(/static/media/phone.1e836ab5.svg);
    background-repeat: no-repeat;
    background-attachment: local;
  }
}

